import uniPassword from '../../../components/uni-password/uni-password.vue'
import uniKeyboard from '../../../components/uni-keyboard.vue'
export default {
	components: {
		uniKeyboard,
		uniPassword
	},
	data() {
		return {
			showKeyboard: false
		}
	},
	methods: {
		onInput(e) {
			//e.cancel = true;
			// console.log(e)
		},
		onConfirm(e) {
			let password = e.value;
			// console.log(password)
		},
		onAutoCheck(e) {
			// console.log(e)
			let password = e.password.join('')
			// 自动验证
			let paypassword = uni.getStorageSync('userInfo').paypassword
			if (paypassword == password) {
				uni.navigateTo({
					url: 'setPwdCenter'
				})
			} else {
				uni.showToast({
					icon: 'none',
					title: '输入错误，请重新输入'
				})
			}
		},
		goGetPwd() {
			uni.navigateTo({
				url: '../getPwd/getPwd'
			})
		}
	},
}
